import axios from "axios"

const apiUrl = '/vacations/search'

export default async function SearchVactions(searchDetails) {
  try {
    const result = await axios.post(apiUrl, searchDetails)
    console.log(result)
    if(result.status === 200) {
      return result.data;
    }
  } catch (error) {
    console.log(error)
    return;
  }
}