import React from 'react';
import { useState } from "react"
import { makeStyles } from '@material-ui/core/styles'
import moment from "moment"
import FollowVacation from "../../asyncfunction/followVacation";
import { useSelector } from "react-redux"
import UnfollowVacation from "../../asyncfunction/unfollowVacation";
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import { Button } from "react-bootstrap"
import './index.css';

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    avatar: {
        backgroundColor: red[500],
    },
    title: {
        fontSize: 100
    }
}));

export default function VacationCard_upgrade(props) {

    const { info } = props;
    const classes = useStyles();
    const userId = useSelector(state => state.userId)
    const [checkboxState, setcheckboxState] = useState(info.user_id == userId ? "checked" : "")

    async function changeFollowingStatus(status) {
        if (status) {
            await FollowVacation(userId, info.id)
            setcheckboxState("checked")
        }
        else {
            await UnfollowVacation(userId, info.id)
            setcheckboxState("")
        }
    }

    return (
        <div className="row onMouseCard" style={{ padding: "20px", paddingLeft:"60px" }}>
            <Card className="col-12" style={{ height: "100%", padding: "20px", border: "1px solid rgb(173, 180, 177)" }}>
                <div className="row">
                    <div className="col-8" >
                        <div className="row"  >
                            <div className="col-3">
                                <div>
                                    <Checkbox icon={<FavoriteBorder style={{ fontSize: 30 }} />} checkedIcon={<Favorite style={{ fontSize: 30 }} />}
                                        checked={checkboxState}
                                        style={{ marginLeft: "10px", fontSize: "50px" }}
                                        onChange={(e) => { changeFollowingStatus(e.target.checked) }} />
                                </div>

                                <div className="card_price">
                                    {info.price} ₪

                                </div>
                                <Button style={{ width: "100px" , backgroundColor:"rgb(82, 27, 235)" }}  type="button" onClick={console.log("button click")}>
                                    -- הזמנה
                                </Button>


                            </div>
                            <div className="col-9" style={{ textAlign: "right" }}>
                                <div className="card_title">
                                    {info.hotel_name}
                                </div>
                                <div className="card_location">
                                    {info.destination}

                                </div>
                                <div>
                                    {info.description}
                                </div>
                                <Typography style={{ paddingTop: "20px" }} variant="body1" color="textSecondary" component="p">
                                    {moment(info.check_in_date).format("DD/MM/YYYY")} - {moment(info.check_out_date).format("DD/MM/YYYY")}
                                </Typography>
                                <div className="cancelation">
                                    ביטול חינם - אין צורך במקדמה

                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col-4">
                        <CardMedia
                            className={classes.media}
                            image={info.image}
                        />
                    </div>
                </div>
            </Card>
        </div>
    )
}