import React, { useState } from 'react';
import { Form } from "react-bootstrap"
import Button from '@material-ui/core/Button';
import SearchVactions from '../../asyncfunction/searchVactions';



export default function SearchVacation() {

    const keys = {
        destination : "" , 
        check_in_date : "",
        check_out_date : ""
    }

    const [searchValues , setValues] = useState(keys)

    async function searchAction(){
        if(!searchValues.destination || !searchValues.check_in_date || !searchValues.check_out_date){
            alert("missing params")
            return;
        }
            
        const result = await SearchVactions(searchValues);
    }

    function updateKeys(category, value) {
        setValues({ ...searchValues, [category]: value })

    }

    console.log(searchValues)

    return (
        <div style={{ padding: "20px" }}>
            <div className="searchTab">
                <div  style={{ paddingRight: "20px", fontSize: "30px" }}>
                    חיפוש
                </div>

                <Form style={{ padding: "20px", fontSize: "20px" }}>
                    <Form.Group >
                        <Form.Label>שם יעד/מקום אירוח</Form.Label>
                        <Form.Control name="destination" type="text" placeholder="לאן תרצו לנסוע ?" onChange={(e)=>{updateKeys(e.target.name , e.target.value)}}/>
                    </Form.Group>
                    <Form.Group>
                        <Form.Label>תאריך צ'ק-אין</Form.Label>
                        <Form.Control name="check_in_date" type="date"  onChange={(e)=>{updateKeys(e.target.name , e.target.value)}}/>
                    </Form.Group>
                    <Form.Group>
                        <Form.Label>תאריך צ'ק-אווט</Form.Label>
                        <Form.Control name="check_out_date" type="date"  onChange={(e)=>{updateKeys(e.target.name , e.target.value)}}/>
                    </Form.Group>
                    {/* <Form.Group>
                        <Form.Label>מספר נוסעים</Form.Label>
                        <Form.Control type="text" name="passangers_num" onChange={(e)=>{updateKeys(e.target.name , e.target.value)}}/>
                    </Form.Group> */}
                </Form>
                <div style={{ padding: "20px" }}>
                    <Button size="large" className="searchButton" variant="contained" color="primary" onClick={searchAction}>
                        <h4> חיפוש</h4>
                    </Button>
                </div>

            </div>
        </div>

    );
}