import { useState } from "react"
import { makeStyles } from '@material-ui/core/styles';
//import { Form, Button, Row, Col } from "react-bootstrap"
import Button from '@material-ui/core/Button';
import "./index.css"
import RegisterUSer from "../../../asyncfunction/newRegister"
import { useHistory } from "react-router-dom"
import { useDispatch } from "react-redux"

const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
            width: '100%',
        },
    },
}));

export default function RegisterPage() {
    const classes = useStyles();
    const details = {
        first_name: "",
        last_name: "",
        user_name: "",
        password: ""
    }

    const [userDetails, setUserDetails] = useState(details)
    const history = useHistory();
    const dispatch = useDispatch()
    const [validateText, setValidateText] = useState("")
    const [displayText, setDisplayText] = useState("none")

    function updateDetails(lable, value) {
        setUserDetails({ ...userDetails, [lable]: value })
    }

    async function CreateRegistration() {
        if (!userDetails.first_name || !userDetails.last_name || !userDetails.user_name || !userDetails.password) {
            setValidateText("missing details")
            setDisplayText("block")
            return;
        }
        const response = await RegisterUSer(userDetails)
        if (response) {
            dispatch({ type: "user id", payload: response.id })
            dispatch({ type: "name of user", payload: userDetails.first_name })
            history.push('/home/forUser')
        }
        else {
            setValidateText("something went wrong...")
            setDisplayText("block")
        }
    }

    return (
        <div className="row">
            <div className="col-xl-4 col-md-8 col-12" style={{ margin: "auto", paddingTop: "10%" }}>
                <div style={{ border: "5px solid rgb(69, 84, 223)", padding: "30px" }}>
                    <div className="title_auth" style={{ fontSize: "40px", textAlign: "center" }}>
                        REGISTRATION
                    </div>

                    <div className="row">
                        <div className="form-group col-xl-6 col-md-6 col-12" style={{ margin: "auto auto" }}>
                            <label >First name</label>
                            <input name="first_name" type="text" class="form-control" placeholder="Enter your first name"
                                onChange={(e) => { updateDetails(e.target.name, e.target.value) }} />
                        </div>
                        <div className="form-group col-xl-6 col-md-6  col-12 ">
                            <label >Last name</label>
                            <input name="last_name" type="text" class="form-control" placeholder="Enter your last name"
                                onChange={(e) => { updateDetails(e.target.name, e.target.value) }} />
                        </div>
                        <div className="form-group col-xl-6 col-md-6  col-12 ">
                            <label >User name</label>
                            <input name="user_name" type="text" class="form-control" placeholder="Enter a User name"
                                onChange={(e) => { updateDetails(e.target.name, e.target.value) }} />
                        </div>
                        <div className="form-group col-xl-6 col-md-6 col-12 ">
                            <label >Password</label>
                            <input name="password" type="password" class="form-control" placeholder="Enter a password"
                                onChange={(e) => { updateDetails(e.target.name, e.target.value) }} />
                        </div>
                        <div className="col-12" style={{ fontSize: "15px", color: "red", display: "block", display: displayText }}>
                            {validateText}
                        </div>

                        <div className={classes.root} style={{width:"100%"}}>
                            <Button style={{ borderRadius: "15px", height: "40px" }} variant="contained" color="primary" onClick={CreateRegistration} >
                                register
                            </Button>
                        </div>
                
                    </div>
                </div>

            </div>
        </div>
    )
}