import './index.css'
import React from 'react';
import { useEffect } from "react"
import { useSelector } from "react-redux"
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { useHistory } from "react-router-dom"
import { useCookies } from 'react-cookie';

const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
            borderRadius: "15px",
            height: 50,
            width: "50%"
        }
    },
}));

export default function WelcomePage() {

    const classes = useStyles()
    const history = useHistory()
    const selector = useSelector(state => state)
    const [cookies, setCookie] = useCookies(['token']);

    useEffect(() => {
        console.log("selector from welcome page", selector)
        if (localStorage.getItem('userId')) {
            setCookie('token', localStorage.getItem('token'), { path: '/' });
            if (localStorage.getItem('userRole') === 1)
                history.push('/home/forAdmin')
            else
                history.push('/home/forUser')
        }
    }, [])

    return (
        <div >
            <div className="row">
                <div className="col-xl-5 col-md-3 col-12 content ">
                    <div className="title pr-4">
                        WELCOME
                    </div>
                    <div className={classes.root}>
                        <Button variant="contained" color="primary" onClick={() => { history.push('/register') }}>
                            REGISTER
                        </Button>
                    </div>
                    <div className={classes.root}>
                        <Button variant="contained" onClick={() => { history.push('/login') }} >
                            LOGIN
                        </Button>
                    </div>
                </div>
                <div className="col-xl-7 col-md-9 col-12 logo">

                </div>

            </div>

        </div>
    )
}